.button{
  display: block;
  width: 200px;
  height: 100%;
  border: none;
  background-color: rgb(5, 56, 5);
  color: white;
  z-index: 5;
}

.App {
  text-align: center;
  background-image: url('https://preview.redd.it/zdarxtlshg611.jpg?auto=webp&s=0c8ac2ad7d4339b8e94a01f132211ec12854abdf');
  background-image: absolute;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  height: 100vh;
}

.App-header{
  display: flex;
  flex-direction: column;
  color: white;
  background-color: rgb(68, 68, 68);
  height: 10vmin;
  z-index: 5;
}

.main-product-view {
  display: flex;
  justify-content: center;
  height: 400px;
  margin: 0 auto;
  z-index: 5;
}

.individual-product-view{
  max-width: 200px;

  background-color: rgba(240, 248, 255, 0.336);
  z-index: 5;
}

.shop-icon-button{
  background-color: transparent;
  left: 93%;
  width: 100px;
  height: 100px;
  border:none;
  z-index: 5;
  position: fixed;
}

.shop-icon-button:hover{
  background-color: transparent;
  left: 93%;
  width: 100px;
  height: 100px;
  border:none;
  z-index: 5;
  position: fixed;
  transform: scale(1.2);
}

.shop-icon-button:active{
  transform: translateY(2px);
  z-index: 5;
}

.shop-icon-button2{
  background-color: transparent;
  left: 60%;
  top: 41%;
  width: 50px;
  height: 50px;
  border:none;
  z-index: 5;
  position: fixed;
  
}
.shop-icon-button2 p{
  position: relative;
  top: 20px;
  left: -30px;
}

.shop-icon-button2:hover{
  background-color: transparent;
  left: 60%;
  top: 41%;
  width: 50px;
  height: 50px;
  border:none;
  z-index: 5;
  position: fixed;
  transform: scale(1.1);
}

.shop-icon-button2:active{
  transform: translateY(2px);
  z-index: 5;
}

.shop-icon {
  width: 40px;
  height: 40px;
  z-index: 5;
  top: -20px;
  position: relative;
}

.img {
  max-width: 100%;
  z-index: 5;
}

.modal {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(58, 58, 58, 0.37);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9;
  border-radius: 10px;
}

.modal-show{
  opacity: 1;
  pointer-events: none;
  z-index: 9;
}

.modal-content{
  width: 500px;
  background-color: whitesmoke;
  z-index: 9;
  border-radius: 10px;
}

.modal-header, .modal-footer{
  padding: 10px;
  z-index: 9;
  border-radius: 10px;
}

.modal-title{
  margin: 0;
  z-index: 9;
}

.modal-body{
  padding: 10px;
  border-top: 1px solid rgb(212, 212, 212);
  border-bottom: 1px solid rgb(212, 212, 212);
  z-index: 9;
}

